<template>
  <div id="tablet">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>详情</span>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="goback">返回</Button>
    </div>
    <div class="map-box">
      <div class="topLeft-box" :class="{activeBox: boxstate === 1}">
        <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="mapOpen" size="small" class="boxBtn"></Button>
        <Map :boxstate="boxstate" :data="alltablemetriesArr" ref="maps" @layerConClick="layerConClick"></Map>
      </div>
      <div class="topRight-box" :class="{activeBox: boxstate === 2}">
        <div class="tab">
          <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="mapOpen2" size="small" class="boxBtn"></Button>
          <Table :data="alltablemetriesArr" :columns="columns2" border :maxHeight="tableHeight"></Table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  pointQualityDetail
} from '@/api/quality'
import Map from './map'
export default {
  name: 'checkInfoMap',
  data () {
    return {
      map: {}, // 地图实例
      view: {}, // 地图视图实例
      /* 放大块的标识 */
      boxstate: 0,
      /* 全部斑块数组 */
      alltablemetriesArr: [],
      /* 详情中斑块表格 */
      columns2: [
        {
          title: '人类活动斑块编号',
          key: 'cd001',
          align: 'center',
          ellipsis: true,
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.$refs.maps.onclickDingwei(params.index)
                    // let arr = []
                    // arr.push(params.row)
                    // this.dingwei(arr)

                    // this.ondbclickTd(params.row.lpLon, params.row.lpLat, params.row.lpId, 'inflectionPoint')
                  }
                }
              }, params.row.cd001)
            ])
          }
        },
        {
          title: '人类活动斑块名称',
          key: 'nname',
          align: 'center',
          ellipsis: true
        },
        {
          title: '原人类活动名称',
          key: 'oldType',
          align: 'center',
          ellipsis: true
        },
        {
          title: '当前人类活动名称',
          key: 'nowType',
          align: 'center',
          ellipsis: true
        }
      ],
      allPoint: [],
      tableHeight: window.innerHeight - 260
    }
  },
  components: {
    // 地图
    Map
  },
  props: {
    seeId: {}
  },
  methods: {
    /* 地图框放大缩小开关控制 */
    mapOpen () {
      this.boxstate = this.boxstate === 1 ? 0 : 1
    },
    /* 点击图层控制器打开地图框 */
    layerConClick () {
      this.boxstate = 1
    },
    /* 表格放大缩小开关控制 */
    mapOpen2 () {
      this.boxstate = this.boxstate === 2 ? 0 : 2
    },
    /* axios: 获取明细列表数据 */
    getDesList2 (e) {
      pointQualityDetail({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.alltablemetriesArr = res.data.data
          console.log(res.data.data)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 地图定位 */
    // dingwei (geo) {
    //   this.$refs.maps.layerAddData(geo)
    // },
    goback () {
      this.$emit('signOut', false)
      // this.dingwei([])
    }
  }
}
</script>
<style scoped lang="scss">
  #tablet{
    position: relative;
    height: 100%;
    overflow: hidden;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .map-box{
      width: 100%;
      height: calc(100% - 80px);
      padding: 0 15px 15px;
      position: relative;
      .topLeft-box {
        position: absolute;
        top: 0;
        left: 15px;
        width: calc(40% - 20px);
        height: 100%;
        border: 1px solid #ccc;
        /* 地图框样式 */
        #viewDiv{
          width: 100%;
          height: 100%;
        }
      }
      .topRight-box {
        position: absolute;
        top: 0;
        right: 15px;
        width: calc(60% - 20px);
        height: 100%;
        .tab{
          border: 1px solid #ccc;
          height: 100%;
          overflow: hidden;
        }
      }
      .bottom-box {
        background: #fff;
        position: absolute;
        top: 0px;
        right: -80%;
        width: calc(60% - 20px);
        height: calc(100% - 40px);
        border: 1px solid #ccc;
        overflow: auto;
        z-index: 100;
      }
      .rightShow{
        right: 15px;
      }
      .sign-btn {
        position: absolute;
        right: 15px;
        bottom: 0px;
        z-index: 99;
      }
      /* box放大 */
      .activeBox, .smallToBig{
        position: absolute;
        left: 15px !important;
        top: 0 !important;
        width: calc(100% - 30px) !important;
        height: 100% !important;
        z-index: 999;
        border: none;
        transition: all 0.8s ease;
        background: #fff;
      }
      .boxBtn {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 99;
      }
    }
  }
</style>
