<template>
  <div id="QualityAssessment">
    <Form :label-width="120" class="searchesBox" id="humanPc">
      <FormItem prop="province" label="人类活动监管台账：">
        <Button type="success" size="small" @click="activitiesModal = true" v-model="activitiesValue"  v-html="activitiesContent" style="margin-right: 10px"></Button>
      </FormItem>
      <div class="queryBtns" style="float: right;margin-top: 6px;">
        <Button type="success" size="small" icon="md-eye" class="btn-add" @click="handleDetails" style="margin-right: 10px" v-show="detailsBtn">查看明细</Button>
        <Button type="primary" size="small" icon="md-eye" @click="handleCheck" v-show="checkBtn" style="margin-right: 10px">查看详情</Button>
        <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="exportBtn">导出</Button>
      </div>
    </Form>
    <div class="assessmentBox">
      <div class="left">
        <Table border stripe :loading="loading" :columns="columns1"
               :data="data1" :maxHeight="tableHeight"></Table>
      </div>
      <div class="right">
        <BarChart :data="data1"></BarChart>
        <Spin size="large" fix v-if="spinShow"></Spin>
      </div>
    </div>
    <!-- 人类活动列表-->
    <Modal
      v-model="activitiesModal"
      title="人类活动批次列表"
      @on-cancel="handleCancel"
      width="1000">
      <div>
        <Table border stripe :loading="aLoading" :columns="aColumns1"
               :data="aData1" highlight-row :height="aTableHeight"
               ref="activitiesMultipleTable"
               @on-current-change="changeAccount"
               id="reportAnalysisTable"></Table>
        <div class="page_box" style="width: 100%;padding: 15px;text-align: right">
          <Page :total="aTotal" :page-size="aPageSize" :current.sync="aPageNum" @on-change="aPageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="handleCancel" class="mr10">取消</i-button>
        <Button type="primary" @click="selectActivities">确定</Button>
      </div>
    </Modal>
    <!-- 查看详情-->
    <div class="detailsTableMap" :class="{addActive:addActive}">
      <detailsTableMap ref="detailsTableMap" :seeId="taskId" @signOut="signOut"></detailsTableMap>
    </div>
    <!-- 查看明细-->
    <div class="detailsTableMap2" :class="{desActive:desActive}">
      <detailsTableMap2 ref="detailsTableMap2" :seeId="taskId" @signOut2="signOut2"></detailsTableMap2>
    </div>
  </div>
</template>

<script>
import BarChart from './BarChart.vue'
import detailsTableMap from './detailsTableMap.vue'
import detailsTableMap2 from './detailsTableMap2.vue'
import {
  getAccountList
} from '@/api/remoteSensing'
import { renderTime } from '@/utils/common'
import {
  // 人类活动巡查结果质量评估列表
  getAssessmentList,
  // 导出
  pointQualityExport
} from '@/api/quality'
export default {
  name: 'QualityAssessment',
  data () {
    return {
      // 选中人类活动批次id
      activitiesValue: '',
      // 选中人类活动批次name
      activitiesContent: '请选择人类活动监管台账',
      // 人类活动列表弹窗
      activitiesModal: false,
      /* 表格高度 */
      aTableHeight: Math.floor((window.innerHeight - 300 - 70) / 48) * 48 + 40,
      /* 表格数据 */
      aColumns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.aPageNum - 1) * this.aPageSize + 1)
          }
        },
        {
          title: '人类活动监管台账名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        }, {
          title: '组别',
          key: 'type',
          render: (h,params) => {
            return h('span', {
              1: '不重要',
              2: '一般',
              3: '重要'
            }[params.row.type])
          }
        },
        {
          title: '日期',
          align: 'center',
          minWidth: 200,
          render: (h, params) => {
            return h('span', renderTime(params.row.updateDate === null ? params.row.createDate : params.row.updateDate))
          }
        },
        {
          title: '人类活动监管台账说明',
          align: 'center',
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '人类活动斑块数量',
          align: 'center',
          minWidth: 200,
          key: 'sonCount'
        },
        {
          title: '人类活动面积(公顷)',
          align: 'center',
          minWidth: 200,
          key: 'sonArea'
        },
      ],
      aData1: [],
      /* 总条数 */
      aTotal: 0,
      aPageNum: 1,
      aPageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      aLoading: false,
      /* 当前选中表格行数据 */
      currentRow: null,
      /* 查询列表 */
      /* 表格高度 */
      tableHeight: window.innerHeight - 252,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '人类活动类型',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '遥感核查斑块数量',
          align: 'center',
          minWidth: 150,
          key: 'orignCount',
          render: (h, params) => {
            if (params.row.orignCount === null || params.row.orignCount === '') {
              return h('span', 0)
            } else {
              return h('span', params.row.orignCount)
            }
          }
        },
        {
          title: '核查斑块数量',
          align: 'center',
          minWidth: 170,
          key: 'nowsCount',
          render: (h, params) => {
            if (params.row.nowsCount === null || params.row.nowsCount === '') {
              return h('span', 0)
            } else {
              return h('span', params.row.nowsCount)
            }
          }
        }
      ],
      data1: [],
      /* 加载 */
      loading: false,
      spinShow: false,
      tempData1: [],
      tempData2: [],
      // 查看详情相关参数
      taskId: '',
      addActive: false,
      // 查看明细相关参数
      desActive: false,
      checkBtn: false,
      detailsBtn: true
    }
  },
  components: {
    BarChart,
    detailsTableMap,
    detailsTableMap2
  },
  created () {
    /* 获取人类活动列表信息 */
    this.getTableList()
    this.getResultList(this.activitiesValue)
  },
  watch: {
    activitiesValue (val) {
      if (val !== '') {
        this.checkBtn = true
        this.detailsBtn = false
        this.taskId = val
      } else {
        this.checkBtn = false
        this.detailsBtn = true
      }
    }
  },
  methods: {
    /* axios: 获取人类活动列表数据 */
    getTableList () {
      this.aLoading = true
      getAccountList({
        data: {
          pageSize: this.aPageSize,
          pageNum: this.aPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.aData1 = res.data.data.rows
          this.aTotal = res.data.data.total
          this.aLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 人类活动列表分页跳转 */
    aPageChange (pageNum) {
      this.aPageNum = pageNum
      this.getTableList()
    },
    /* 取消选择人类活动 */
    handleCancel () {
      this.$refs.activitiesMultipleTable.clearCurrentRow()
      this.activitiesModal = false
    },
    // 保存勾选人类活动
    selectActivities () {
      this.activitiesContent = this.currentRow.name
      this.activitiesValue = this.currentRow.id
      this.handleCancel()
      // 根据条件查询发生变化斑块列表
      this.getResultList(this.activitiesValue)
    },
    /* 选中台账的函数 */
    changeAccount (currentRow, oldCurrentRow) {
      this.currentRow = currentRow
    },
    /* axios: 根据条件查询列表 */
    getResultList (e) {
      this.spinShow = true
      this.loading = true
      getAssessmentList({
        data: {
          id: e // 人类活动标识
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data
          this.spinShow = false
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 导出文件 */
    handleExport () {
      pointQualityExport({
        data: {
          id: this.taskId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data.filepath
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查看详情 */
    handleCheck () {
      this.addActive = true
      this.$refs.detailsTableMap.getDesList2(this.taskId)
    },
    /* 查看详情页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 查看明细 */
    handleDetails () {
      this.desActive = true
      this.$refs.detailsTableMap2.getDesList(this.taskId)
    },
    /* 查看明细页面退出 */
    signOut2 (addActive) {
      this.desActive = addActive
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #QualityAssessment {
    padding: 10px;
    width: 100%;
    height: 100%;
    .searchesBox {
      width: 49%;
      overflow: hidden;
      .ivu-form-item {
        margin-bottom: 0;
        float: left;
      }
      margin-bottom: 10px;
    }
    .assessmentBox {
      width: 100%;
      height: calc(100% - 42px);
      overflow: hidden;
      .left {
        float: left;
        width: 49%;
        margin-left: 5px;
        height: calc(100% - 5px);
      }
      .right {
        position: relative;
        float: right;
        width: 49%;
        height: calc(100% - 5px);
        margin-right: 5px;
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .queryBtns {
      float: right;
      margin-top: 6px;
    }
    .detailsTableMap {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .detailsTableMap2 {
      height: 100%;
      width: 50%;
      position: absolute;
      bottom: -120%;
      left: 0;
      padding: 15px;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .desActive{
      bottom:0;
    }
  }
</style>
