<template>
  <div id="tablet">
    <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut2',false)">退出</Button>
    <div style="height: calc(100% - 34px);margin-top: 10px">
      <Table :data="alltablemetriesArr" :columns="columns2" border :maxHeight="tableHeight"></Table>
    </div>
  </div>
</template>
<script>
import {
  pointQualityDetail
} from '@/api/quality'
export default {
  name: 'checkInfoMap',
  data () {
    return {
      /* 全部斑块数组 */
      alltablemetriesArr: [],
      /* 详情中斑块表格 */
      columns2: [
        {
          title: '人类活动斑块编号',
          key: 'cd001',
          align: 'center',
          ellipsis: true
        },
        {
          title: '人类活动斑块名称',
          key: 'aname',
          align: 'center',
          ellipsis: true
        },
        {
          title: '原人类活动名称',
          key: 'oldType',
          align: 'center',
          ellipsis: true
        },
        {
          title: '当前人类活动名称',
          key: 'nowType',
          align: 'center',
          ellipsis: true
        }
      ],
      tableHeight: window.innerHeight - 252
    }
  },
  components: {
  },
  props: {
    seeId: {}
  },
  methods: {
    /* axios: 获取明细列表数据 */
    getDesList (e) {
      pointQualityDetail({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.alltablemetriesArr = res.data.data
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>
<style scoped lang="scss">
  #tablet{
    height: 100%;
    text-align: right;
  }
</style>
