<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'BarChart',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.name === null) {
          item.name = ''
        }
        Arr.push(item.name)
      }
      return Arr
    },
    orignCount: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.orignCount)
      }
      return Arr
    },
    nowsCount: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.nowsCount))
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '40px',
          right: '40px',
          top: '20%',
          containLabel: true
        },
        legend: {
          data: ['原人类活动斑块数量', '核查后人类活动斑块数量'],
          top: 20,
          right: 30
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '数量/个',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        dataZoom: [
          {
            type: 'slider',
            show: true
          }
        ],
        series: [
          {
            name: '原人类活动斑块数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(116, 159, 131)'
              }
            },
            yAxisIndex: 0,
            data: this.orignCount,
            barMaxWidth: 30
          },
          {
            name: '核查后人类活动斑块数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(212,130,101)'
              }
            },
            yAxisIndex: 0,
            data: this.nowsCount,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

